import {Component, ViewChild, ElementRef, AfterViewInit, ViewEncapsulation} from '@angular/core';
import { ApiService } from '../shared';

declare const jQuery: any;

@Component({
    selector: 'app-body',
    template: require('./body.html'),
    styles: [require('./body.css')],
    encapsulation: ViewEncapsulation.None
})
export class BodyComponent implements AfterViewInit {

    readonly firstMenuItem = "Login";
    readonly secondMenuItem = "Logout";
    readonly menuTitle = "Menu";

    title: string;

    @ViewChild("sidebar") sidebar: ElementRef;

    constructor(private api: ApiService) {
        this.title = this.api.title;
    }

    ngAfterViewInit(): void {
        jQuery(this.sidebar.nativeElement)
            .sidebar('attach events', '.toc.item')
            .sidebar('setting', 'transition', 'overlay')
    }
}



